  font: inherit;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
  padding: 0;
  background: none;
  border: none;
  width: auto;
  margin: 0;
  padding-block: 0;
  padding-inline: 0;
  line-height: normal;
  overflow: visible;
}

.mySwiper {
  background-color: #d6ecec;
}

