}

@media (width >= 1440px) {
  .swiper__inner {
    padding-bottom: 0;
  }
}

.swiper-slide {
  background: #d6ecec;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  display: flex;
}

@media (width >= 768px) {
  .swiper-slide {
    justify-content: flex-start;
  }
}

.swiper-slide img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.slide-one {
  background-color: #d6ecec;
  padding: 30px;
}

@media (width >= 768px) {
