    padding-left: 85px;
  }
}

.swiper-button-prev:after, .swiper-button-next:after, .swiper-rtl .swiper-button-prev:after, .swiper-rtl .swiper-button-next:after {
  content: none;
}

.swiper-button-next, .swiper-button-prev {
  width: 48px;
  height: 14px;
  display: block;
}

.swiper-pagination-fraction {
  color: #0c797a;
  font-size: 11px;
