  font-size: 16px;
  line-height: 24px;
  position: relative;
  top: -4px;
  right: -1px;
}

.swiper-pagination-total {
  color: #0c797a;
  font-size: 11px;
  line-height: normal;
}

.container {
  margin: 0 auto;
  padding-inline: 30px;
}

@media (width >= 768px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1440px) {
  .container {
    max-width: 1440px;
    margin-inline: auto;
    padding-inline: 123px;
  }
}

.page {
  box-sizing: border-box;
  color: #7c7c7c;
  scroll-behavior: smooth;
  font-family: Inter, helvetica, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.page__body {
  background: #f7f7f7;
  margin: 0;
}

.page__body:has(.page__menu:target) {
  height: 100%;
  position: relative;
  overflow: hidden;
}

.page__menu {
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  transition: transform .3s, opacity .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  display: block;
  transform: translateX(0);
}

.unscroll {
  overflow: hidden;
}

.none {
  display: none;
}

.button {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background-color: #333;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 16px 0 17px;
  font-family: Helvetica, arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  transition: background-color .3s;
  display: flex;
}

.button:hover {
  background-color: #131313;
}

.button-cyan {
  cursor: pointer;
  color: #0db2b3;
  text-transform: uppercase;
  background-color: #0000;
  border: none;
  margin-bottom: 6px;
  padding: 0;
  font-family: Inter, helvetica, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 14px;
  display: inline-block;
  position: relative;
}

.button-cyan:after {
  content: "";
  background-color: #0db2b3;
  width: 50%;
  height: 1px;
  transition: width .3s;
  position: absolute;
  bottom: -6px;
  left: 0;
  right: 0;
}

.button-cyan:hover:after {
  width: 100%;
}

.button-form {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background-color: #0db2b3;
  border: none;
  justify-content: center;
  align-items: center;
  width: 102px;
  height: 40px;
  padding: 13px 35px;
  font-family: Inter, helvetica, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 14px;
  transition: background-color .3s;
  display: flex;
}

.button-form:hover {
  background-color: #0c797a;
}

.icon {
  background-size: cover;
  width: 24px;
  height: 24px;
  transition: transform .3s;
  display: block;
}

.icon:hover {
  transform: scale(1.2);
}

